import { markdownRenderer } from 'inkdrop';
import { remarkRuby } from './remark';

export default {

  activate() {
    // console.log("hello")
    this.subscription = inkdrop.commands.add(document.body, {
      'furigana:furigana': () => {
        if(/ï½œ(\S+|)ã€Š(\S+|)ã€‹/.test(inkdrop.getActiveEditor().cm.getSelection())){
          inkdrop.getActiveEditor().cm.replaceSelection(inkdrop.getActiveEditor().cm.getSelection().split("ã€Š")[0].replace("ï½œ",""));
        }else{
          inkdrop.getActiveEditor().cm.replaceSelection(`ï½œ${inkdrop.getActiveEditor().cm.getSelection()}ã€Šã€‹`);
          const {line, ch} = inkdrop.getActiveEditor().cm.getCursor();
          inkdrop.getActiveEditor().cm.setCursor({line, ch: ch - 1});
        }
      }
    });
    markdownRenderer.remarkPlugins.push(remarkRuby);
    // console.log(markdownRenderer);
  },

  deactivate() {
    this.subscription.dispose();
    markdownRenderer.remarkPlugins = markdownRenderer.remarkPlugins.filter(a!=remarkRuby);
  }

};
